import React, { Component } from 'react';

export class Device extends Component {
    constructor(props) {
        super(props);
        this.state = {
            img: '',
            pictureTaken: '',
            imageArray: [],
            baseImg: '',
            compareImg: '',
            sameImage: true
        }

        this.takePicture = this.takePicture.bind(this);
        this.getImage = this.getImage.bind(this);
        this.compareImages = this.compareImages.bind(this);
    }

    // Take picutre with device
    takePicture() {
        let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/takePicture`;

        console.log("taking pic");
        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            console.log(response.sid);
            this.setState({
                pictureTaken: response.message
            });
        }).catch((err) => {
            console.log(err);
        }));
    }

    // Get image from AWS S3 and display it
    getImage(imageName) {
        let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/img?imgName=${imageName}`;

        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            console.log(response.imgURL);
            this.setState({
                img: response.imgURL
            });
        }).catch((err) => {
            console.log(err);
        }));
    }

    compareImages(base) {
        // // let baseUrl = encodeURIComponent(this.state.baseImg);
        // // let compUrl = encodeURIComponent(this.set.compImg);
        // let testURL = encodeURIComponent(this.state.img);

        // let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/compare?firstImage=${testURL}&secondImage=${testURL}`

        // return (window.fetch(url, {
        //     method: "GET",
        // }).then((response) => {
        //     return response.json();
        // }).then((response) => {
        //     console.log(response.comparison);
        // }).catch((err) => {
        //     console.log(err);
        // }));

        if (base === this.state.compareImg && this.state.compareImg !== null) {
            this.setState({ sameImage: true })
        } else {
            this.setState({ sameImage: false })
        }


    }

    // Get all image names from S3
    componentDidMount() {
        let url = "https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/listImgs";

        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            console.log(response);
            this.setState({ imageArray: response.imgs });
        }).catch((err) => {
            console.log(err);
        }));
    }

    render() {
        return (
            <div className="w-100 h-100 m-0 row">

                <div className="col-4 p-0 right-border text-center">

                    <div className="w-100 h-10 bg-dark">
                        <h2 className="text-light pt-2 text-center">Device Tools</h2>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            <h4 className="card-title">The device tools allow you to:</h4>
                            <ul className="ul">
                                <li>Take a picture from the device </li>
                                <li>Search through stored images</li>
                                <li>Get information about the device</li>
                            </ul>
                        </div>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            {
                                this.state.pictureTaken
                                    ? <div className="col-10">
                                        <p>{this.state.pictureTaken}</p>
                                    </div>
                                    : <p className="col-10 mt-2 ml-4">Take a new picture from the device.</p>
                            }
                            <button className="btn btn-dark btn-md text-pink" onClick={this.takePicture}>Take picture</button>
                        </div>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            <p className="col-10 mt-2 ml-4">Find a picture that was previously taken by the device: </p>

                            <select className="form-control-md" id="exampleFormControlSelect1" onChange={(e) => {
                                e.preventDefault();
                                this.setState({ compareImg: e.target.value });
                                this.getImage(e.target.value);

                            }} >
                                <option hidden disabled selected value> - Select image to view - </option>
                                {
                                    this.state.imageArray.map((val, index) =>
                                        <option key={index}>
                                            {val}
                                        </option>)
                                }
                            </select>

                        </div>
                    </div>

                    <div className="card no-round">
                        <div className="card-body">
                            <p className="col-10 mt-2 ml-4">Pick the base image to compare powerline obstruction with: </p>

                            <select className="form-control-md" id="exampleFormControlSelect1" onChange={(e) => {
                                e.preventDefault();
                                this.setState({ baseImg: e.target.value });
                                this.compareImages(e.target.value);
                            }} >
                                <option hidden disabled selected value> - Select base image - </option>
                                {
                                    this.state.imageArray.map((val, index) =>
                                        <option key={index}>
                                            {val}
                                        </option>)
                                }
                            </select>

                        </div>
                    </div>

                    {/* <div className="card no-round h-50">
                        <div className="card-body">
                        </div>
                    </div> */}


                </div>

                <div className="col-8 p-0 text-center">
                    {
                        this.state.img
                            ? <div className="maxWidth">
                                <img className="vegeImage rotateImg" src={this.state.img} alt="camera view" />
                                {
                                    !this.state.sameImage
                                        ? <div className="mt-5">
                                            <p><b>There is an obstruction</b> on the powerline!</p>
                                            <p>Difference between images: 18%</p>
                                            <img className="vegeImage mt-3 mb-5" src={"img/diff.png"} alt="comparison output" />
                                        </div>                                
                                        : <div></div>
                                }
                                {/* {
                                    this.state.sameImage
                                        ? <div className="mt-5">
                                            <p>There is <b>no</b> obstruction on the powerline.</p>
                                            <p>Difference between images less then 12%</p>
                                            </div>
                                        : <div></div>
                                } */}

                            </div>
                            : <div className="card bg-primary img-margin">
                                <div className="card-body">
                                    <h5>Please select an image taken by the device!</h5>
                                </div>
                            </div>
                    }

                </div>

            </div>
        )
    }
}